<template>
  <v-layout column align-center>
    <v-img class="round ma-3"
      :src="img"
      :lazy-src="lazyImg"
      :min-width="imgSize" :max-width="imgSize" />

    <p class="display-1"
      v-html="title"></p>

    <p class="subheading"
      v-html="description"></p>
  </v-layout>
</template>

<script>
let image = require('./image.json');

export default {
  data() {
    return {
      img: require('@/assets/img/face.jpg'),
      lazyImg: image.base64,

      title: `Hi! I'm Gabriel.`,

      description: `
        I study AI and business analytics at the <br>
        Singapore University of Technology and Design.
      `,
    };
  },

  computed: {
    imgSize() {
      if (this.$vuetify.breakpoint.smAndUp) return 256;
      else return 168;
    },
  },
};
</script>

<style scoped>
.round {
  border-radius: 50%;
}
</style>
